<template>
	<el-main>
		<div class="container-fluid">
			<el-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
				<el-breadcrumb-item :to="{name:'homeLink'}"><i class="glyphicon glyphicon-home"></i></el-breadcrumb-item>
				<el-breadcrumb-item></el-breadcrumb-item>
				<el-breadcrumb-item></el-breadcrumb-item>
			</el-breadcrumb>			
		</div>
		<div class="rtl-custom-box mt15">
			<div class="box-hd">
				<div class="cont">
					<div class="form-horizontal">
						<el-form :inline="true" :model="pageData.queryObj" class="demo-form-inline">
							<el-row>
								<el-col :span="6">
									<el-col :span="8"><label class="control-label"></label></el-col>
									<el-col :span="16">
										<el-form-item>
											<el-input v-model="pageData.tableObj.siteNo" :disabled="addDialogDisabled" placeholder="" clearable />
										</el-form-item>
									</el-col>
								</el-col>
								<el-col :span="6">
									<el-col :span="8"><label class="control-label"></label></el-col>
									<el-col :span="16">
										<el-form-item>
											<el-select v-model="pageData.tableObj.oaStatus">
												<el-option v-for="item in pageData.statusList" :label="item.name" :value="item.value" :key="item.value"></el-option>
											</el-select>
										</el-form-item>
									</el-col>
								</el-col>
								<el-col :span="12">
									<el-col :span="4"><label class="control-label"></label></el-col>
									<el-col :span="20">
										<el-col :span="10">
											<el-date-picker v-model="pageData.tableObj.startTime" value-format="yyyy-MM-dd" align="right" type="date" placeholder=""></el-date-picker>	
										</el-col>
										<el-col :span="2" class="text-center" style="line-height: 40px;">-</el-col>
										<el-col :span="10">
											<el-date-picker v-model="pageData.tableObj.endTime" value-format="yyyy-MM-dd" align="right" type="date" placeholder=""></el-date-picker>	
										</el-col>
									</el-col>
								</el-col>
							</el-row>
							<el-row>
								<el-col :span="6">
									<el-col :span="8"><label class="control-label"></label></el-col>
									<el-col :span="16">
										<el-form-item>
											<el-select v-model="pageData.tableObj.budgetId">
												<el-option label="" value=""></el-option>
												<el-option v-for="item in pageData.budgetList" :label="item.label" :key="item.budgetId" :value="item.budgetId"></el-option>
											</el-select>
										</el-form-item>
									</el-col>
								</el-col>
								<el-col :span="6" class="text-center">
									<el-form-item>
										<el-button type="primary" @click.stop="queryEvt" icon="el-icon-search"></el-button>
									</el-form-item>
								</el-col>
							</el-row>
						</el-form>
					</div>
				</div>
			</div>
		</div>
		<div class="box-bd pr mt15">
			<div class="tab-content custom-content">
				<div class="tab-pane in active">
					<el-row class="mb10">
						<el-col :span="12">
							<el-button type="primary" icon="el-icon-edit-outline" @click.stop="pageData.addDialog.dialogFormVisible = true"></el-button>
						</el-col>
						<!--<el-col :span="12" class="text-right">
							<el-button type="primary" plain icon="el-icon-upload"></el-button>
						</el-col>-->
					</el-row>
					<div class="table-responsive">
						<el-table border :data="pageData.tableObj.list" style="width: 100%">
							<el-table-column sortable prop="id" label="" min-width="120"></el-table-column>
							<el-table-column sortable prop="siteNo" label="" min-width="110"></el-table-column>
							<el-table-column sortable prop="siteName" label="" min-width="120"></el-table-column>
							<el-table-column sortable prop="budgetName" label="" min-width="90"></el-table-column>
							<el-table-column sortable prop="oaId" label="OA" min-width="120"></el-table-column>
							<el-table-column sortable prop="auditorName" label="" min-width="130"></el-table-column>
							<el-table-column sortable prop="oaStatusName" label="" min-width="120"></el-table-column>
							<el-table-column sortable prop="created" label="" min-width="120"></el-table-column>
							<el-table-column sortable prop="creator" label="" min-width="110"></el-table-column>
							<el-table-column sortable prop="auditOpinion" label="" min-width="110"></el-table-column>
							<el-table-column fixed="right" label="" min-width="100">
								<template slot-scope="scope">
									<el-button @click.stop="detailEvt(scope.$index, scope)" type="text" size="small"></el-button>
								</template>
							</el-table-column>
						</el-table>
					</div>
					<div class="page">
						<div class="block p15 text-center">
							<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
							:current-page="pageData.tableObj.currentPage" 
							:page-sizes="pageData.tableObj.pageArray"
							:page-size="pageData.tableObj.pageSize"
							layout="total, sizes, prev, pager, next, jumper"
							:total="pageData.tableObj.totalRows">
						</el-pagination>
					</div>
				</div>
			</div>
		</div>
	</div>
	<el-dialog title="" :visible.sync="pageData.addDialog.dialogFormVisible" width="90%" :before-close="clearEvt">
		<el-switch style="display: block;position: absolute;top: 20px;right: 65px;" v-model="pageData.addDialog.switch" active-color="#13ce66" inactive-color="#ff4949" active-text="" inactive-text=""></el-switch>
		<el-form class="form-horizontal" v-if="pageData.addDialog.switch">
			<el-row>
				<el-col :span="8">
					<el-form-item>
						<el-col :span="8">
							<label class="control-label"></label>
						</el-col>
						<el-col :span="16">
							<el-input :disabled="true" v-model="pageData.addDialog.createRate.id" auto-complete="off" clearable></el-input>	
						</el-col>
					</el-form-item>
				</el-col>
				<el-col :span="8">
					<el-form-item>
						<el-col :span="8">
							<label class="control-label"></label>
						</el-col>
						<el-col :span="16">
							<el-input v-if="!pageData.addDialog.isDetail" v-model="pageData.addDialog.createRate.siteNo" :disabled="addDialogDisabled" clearable auto-complete="off"></el-input>
							<el-input v-if="pageData.addDialog.isDetail" v-model="pageData.addDialog.createRate.siteNo" :disabled="pageData.addDialog.isDetail" clearable auto-complete="off"></el-input>
						</el-col>
					</el-form-item>
				</el-col>
				<el-col :span="8">
					<el-form-item>
						<el-col :span="8">
							<label class="control-label"></label>
						</el-col>
						<el-col :span="16">
							<el-select v-model="pageData.addDialog.createRate.budgetId" @change="siteNoEvt" :disabled="pageData.addDialog.isDetail" >
								<el-option label="" value=""></el-option>
								<el-option v-for="item in pageData.budgetList" :label="item.label" :key="item.budgetId" :value="item.budgetId"></el-option>
							</el-select>	
						</el-col>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="8">
					<el-form-item>
						<el-col :span="8">
							<label class="control-label">OA</label>
						</el-col>
						<el-col :span="16">
							<el-input v-model="pageData.addDialog.createRate.oaId" :disabled="true" clearable auto-complete="off"></el-input>	
						</el-col>
					</el-form-item>
				</el-col>
				<el-col :span="8">
					<el-form-item>
						<el-col :span="8">
							<label class="control-label"></label>
						</el-col>
						<el-col :span="16">
							<el-input v-if="pageData.addDialog.isDetail" v-model="pageData.addDialog.createRate.oaStatusName" :disabled="pageData.addDialog.isDetail" clearable auto-complete="off"></el-input>
							<el-select v-if="!pageData.addDialog.isDetail" v-model="pageData.addDialog.createRate.oaStatus" :disabled="true">
								<el-option v-for="item in pageData.statusList" :key="item.value" :label="item.name" :value="item.value"></el-option>
							</el-select>
						</el-col>
					</el-form-item>
				</el-col>
				<el-col :span="8">
					<el-form-item>
						<el-col :span="8">
							<label class="control-label"></label>
						</el-col>
						<el-col :span="16">
							<el-date-picker v-model="pageData.addDialog.createRate.created" :disabled="true" value-format="yyyy-MM-dd" type="date" placeholder=""></el-date-picker>
						</el-col>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
		<el-table border :data="pageData.addDialog.createRate.oaRateItemList" max-height="490" style="width: 100%;margin-bottom: 10px;">
			<el-table-column sortable prop="budgetName" label=""></el-table-column>
			<el-table-column sortable prop="partnerTeamName" label=""></el-table-column>
			<el-table-column sortable prop="nowRate" label="">
				<template slot-scope="scope">
					<span>{{scope.row.nowRate}}%</span>
				</template>
			</el-table-column>
			<el-table-column label="">
				<template slot-scope="scope">
					<span v-if="pageData.addDialog.isDetail">{{scope.row.newRate}}%</span>
					<el-input v-if="!pageData.addDialog.isDetail" placeholder="" @input="totalNewRateEvt" v-model="scope.row.newRate">
						<template slot="append">%</template>
					</el-input>
				</template>
			</el-table-column>
		</el-table>
		<div class="text-center">
			<el-button v-if="pageData.addDialog.isDetail" type="danger" icon="el-icon-circle-close-outline" @click.stop="clearEvt"></el-button>
			<el-button v-if="!pageData.addDialog.isDetail" type="success" icon="el-icon-upload" @click.stop="addEvt"></el-button>
			<el-button v-if="!pageData.addDialog.isDetail" icon="el-icon-circle-close-outline" @click.stop="clearEvt"></el-button>
		</div>
	</el-dialog>
</el-main>
</template>

<script type="text/ecmascript-6">
import {floatObj} from '../../apis/calculator'
export default {
	data() {
		return {
			addDialogDisabled:false,
			pageData:{
      		statusList:[ // 
      		{name:"",value:"-1"},
      		{name:"",value:"0"},
      		{name:"",value:"1"},
      		{name:"",value:"2"}
      		],
      		budgetList:[], // 
      		tableObj:{
      			oaStatus :"-1", //  OA
				mandt :"300", //  
				startTime :"", //  
				endTime :"", //  
				siteNo :"", //  
				budgetId :"", //  
				currentPage: 1,
				pageSize: 10,
      			totalPage: 1, // 
      			totalRows: 1, // 
      			pageArray:[10,20,30,40,50],
      			list: []
      		},
      		addDialog:{
      			switch:true,
      			tableHeight:0,
      			isDetail:false,
      			dialogFormVisible:false,
      			createRate:{
					mandt :"", // 
					oaId :"", // OA
					siteNo :"", // 
					siteName :"", // 
					oaStatus :"0", // OA
					auditorId :"", // 
					auditorName :"", // 
					delFlag :"", // 
					creator :"", // 
					budgetId :"", // 
					created:this.$api.getDateFunc(true),
					oaRateItemList:[]
					
				},
				formLabelWidth:'120px',
			},
			updateDialog:{
				dialogFormVisible:false,
				form:{},
				formLabelWidth:'120px',
			},
		}
	}
},
created(){
	this.commonInfo()
	let height = document.documentElement.clientHeight
	this.pageData.addDialog.tableHeight = height*0.4
},
methods: {
	initFunc(){
		this.querySubjectType()
	},
	commonInfo(){
		const loading = this.$loading({
			lock: true,
			spinner: 'wm-icon-loading',
			background: 'rgba(0, 0, 0, 0.7)'
		})
		var obj ={}
		fetch(this.$api.urls.POST_QUERYCOMMONINFO,{
			method:'post',
			headers:{
				"Content-type":"application/json",
			},
			credentials: "include",
			body:JSON.stringify(obj)
		})
		.then(res=>res.json())
		.then(res=>{
			setTimeout(() => {
				loading.close();
			}, 500)
			if(res.success){
				this.$store.dispatch("setMandt",res.model.mandt)
				this.$store.dispatch("setIndu",res.model.indu)
				this.$store.dispatch("setSiteNo",res.model.siteNo)
				this.pageData.tableObj.mandt = this.$store.getters.mandt
				this.pageData.addDialog.createRate.siteNo = this.$store.getters.siteNo
				this.pageData.tableObj.siteNo = this.$store.getters.siteNo
				if(res.model.siteNo!=""){
					this.addDialogDisabled = true	
				}
				this.initFunc()
			}else{
				this.$message.error(res.message)
			}
		}).catch(err=>{
			setTimeout(() => {
				loading.close();
			}, 500)
			this.$message.error(""+err)
		})
	},
	totalNewRateEvt(){
		var list = this.pageData.addDialog.createRate.oaRateItemList,
		totalFee = 0
		list.forEach((el)=>{
			var num = 0
			if(el.newRate){
				num = parseFloat(el.newRate)	
			}
			totalFee += num
			if(totalFee>100){
				this.$message({
					message: '100%!',
					type: 'warning'
				})
				el.newRate = ""
				return false
			}
		})
		
	},
	querySubjectType(){
		var obj ={
			mandt : this.pageData.tableObj.mandt,
			subjectTypeId : 1, 
		}
		fetch(this.$api.urls.POST_QUERYSUBJECTTYPE,{
			method:'post',
			headers:{
				"Content-type":"application/json",
			},
			credentials: "include",
			body:JSON.stringify(obj)
		})
		.then(res=>res.json())
		.then(res=>{
			if(res.success){
				res.model.forEach(item=>{
					item.label = item.budgetId+" "+item.budgetName
					this.pageData.budgetList.push(item)	
				})
				this.queryEvt()
			}else{
				this.$message.error(res.message)
			}
		}).catch(err=>{
			this.$message.error(""+err)
		})
	},
	siteNoEvt(){
		if(this.pageData.addDialog.createRate.siteNo == ""){
			this.$message({
				message: '!',
				type: 'warning'
			})
			return false
		}
		const loading = this.$loading({
			lock: true,
          //text: '',
          spinner: 'wm-icon-loading',
          background: 'rgba(0, 0, 0, 0.7)'
      })
		let obj = {
      		siteNo:this.pageData.addDialog.createRate.siteNo, // 
      		budgetId:this.pageData.addDialog.createRate.budgetId, 
      	}
      	fetch(this.$api.urls.POST_QUERYCOMMONINFO,{
      		method:'post',
      		headers:{
      			"Content-type":"application/json",
      		},
      		credentials: "include",
      		body:JSON.stringify(obj)
      	})
      	.then(res=>res.json())
      	.then(res=>{
      		setTimeout(() => {
      			loading.close();
      		}, 500)
      		if(res.success){
	        	// 
	        	const data = res.model
	        	let budgetId = this.pageData.addDialog.createRate.budgetId
	        	let budgetName = ""
	        	for(let i=0;i<this.pageData.budgetList.length;i++){
	        		if(this.pageData.budgetList[i].budgetId == budgetId){
	        			budgetName = this.pageData.budgetList[i].budgetName
	        		}
	        	}
	        	const obj = {
	        		mandt :data.mandt, //  
							oaId :"", //  oa
							siteNo :this.pageData.addDialog.createRate.siteNo, //  
							siteName :data.siteName, //  
							oaStatus :"0", // OA
							auditorId :"", // 
							auditorName :"", // 
							delFlag :"", // 
							creator :"", // 
							budgetId :budgetId, // 
							created:this.$api.getDateFunc(true),
							oaRateItemList:[]
						}
						for(let i=0;i<data.partnerTeam.length;i++){
							obj.oaRateItemList.push({
	        					partnerTeamNo :data.partnerTeam[i].partnerTeamNo, // 
								nowRate:this.$api.accMul(data.partnerTeam[i].nowRate,100),
								newRate :"", //  
								partnerTeamName:data.partnerTeam[i].partnerTeamName, // 
								creator :data.partnerTeam[i].creator, //  
								budgetId :budgetId, // 
								budgetName:budgetName,
								mandt :data.partnerTeam[i].mandt, //  
							})
						}
						
						this.pageData.addDialog.createRate = obj
						
					}else{
						this.pageData.addDialog.createRate = {
							mandt :"", // 
							oaId :"", // OA
							siteNo :this.$store.getters.siteNo, // 
							siteName :"", // 
							oaStatus :"0", // OA
							auditorId :"", // 
							auditorName :"", // 
							delFlag :"", // 
							creator :"", // 
							budgetId :"", // 
							created:this.$api.getDateFunc(true),
							oaRateItemList:[]
						}
						this.pageData.addDialog.dialogFormVisible = true
						this.pageData.addDialog.isDetail = false
						this.$message.error(res.message)
					}
				}).catch(err=>{
					setTimeout(() => {
						loading.close();
					}, 500)
					this.$message.error(""+err)
				})
			},
			clearEvt(){
				this.pageData.addDialog.createRate = {
					mandt :"", // 
					oaId :"", // OA
					siteNo :this.$store.getters.siteNo, // 
					siteName :"", // 
					oaStatus :"0", // OA
					auditorId :"", // 
					auditorName :"", // 
					delFlag :"", // 
					creator :"", // 
					budgetId :"", // 
					created:this.$api.getDateFunc(true),
					oaRateItemList:[]
				}
				this.pageData.addDialog.dialogFormVisible = false
				this.pageData.addDialog.isDetail = false
			},
			addEvt(){
				if(this.pageData.addDialog.createRate.siteNo == ""){
					this.$message({
						message: '!',
						type: 'warning'
					})
					return false
				}
				for(var i = 0;i<this.pageData.addDialog.createRate.oaRateItemList.length;i++){
					if(this.pageData.addDialog.createRate.oaRateItemList[i].newRate == ""){
						this.$message({
							message: '!',
							type: 'warning'
						})
						return false
					}
				}
				let totalNewRate = 0
				let obj = this.pageData.addDialog.createRate
				obj.oaRateItemList.forEach((item)=>{
					totalNewRate += parseFloat(item.newRate)
				})
				if(totalNewRate!=100){
					this.$message({
						message: `100%${totalNewRate}%!`,
						type: 'warning'
					})
					return false
				}
				obj.oaRateItemList.forEach((item)=>{
					item.nowRate = item.nowRate/100
					item.newRate = item.newRate/100
				})
				const loading = this.$loading({
					lock: true,
			        spinner: 'wm-icon-loading',
			        background: 'rgba(0, 0, 0, 0.7)'
			    })
				fetch(this.$api.urls.POST_RATE_CREATERATE,{
					method:'post',
					headers:{
						"Content-type":"application/json",
					},
					credentials: "include",
					body:JSON.stringify(obj)
				})
				.then(res=>res.json())
				.then(res=>{
					setTimeout(() => {
						loading.close();
					}, 500)
					if(res.success){
						this.pageData.addDialog.dialogFormVisible = false
						this.$alert('', '', {
							confirmButtonText: '',
							type: 'success',
							center: true,
							callback: action => {
								this.queryEvt()  	
								this.clearEvt()
							}
						})
					}else{
						obj.oaRateItemList.forEach((item)=>{
							item.nowRate = this.$api.accMul(item.nowRate,100)
							item.newRate = this.$api.accMul(item.newRate,100)
						})
						this.pageData.addDialog.createRate = obj
						this.$message.error(res.message)
					}
				}).catch(err=>{
					setTimeout(() => {
						loading.close();
					}, 500)
					obj.oaRateItemList.forEach((item)=>{
						item.nowRate = this.$api.accMul(item.nowRate,100)
						item.newRate = this.$api.accMul(item.newRate,100)
					})
					this.pageData.addDialog.createRate = obj
					this.$message.error(""+err)
				})
			},
			detailEvt(index,item){
				this.pageData.addDialog.isDetail = true
				const loading = this.$loading({
					lock: true,
					spinner: 'wm-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				})
				var obj = {
					mandt :this.pageData.tableObj.mandt, 
					mainId :item.row.id, // id
					siteNo:item.row.siteNo
				}
				fetch(this.$api.urls.POST_RATE_QUERYRATEITEM,{
					method:'post',
					headers:{
						"Content-type":"application/json",
					},
					credentials: "include",
					body:JSON.stringify(obj)
				})
				.then(res=>res.json())
				.then(res=>{
					setTimeout(() => {
						loading.close();
					}, 500)
					if(res.success){
						const data = res.model
						data.forEach((item)=>{
							item.nowRate = this.$api.accMul(item.nowRate,100)
							item.newRate = this.$api.accMul(item.newRate,100)
						})
						var oaStatusName = ""
						for (var i = 0; i <this.pageData.statusList.length; i++) {
							if(this.pageData.statusList[i].value == item.row.oaStatus){
								oaStatusName = this.pageData.statusList[i].name
								break
							}
						}
						this.pageData.addDialog.createRate = {
				    		id :item.row.id, //  
								mandt :item.row.mandt, //  
							oaId :item.row.oaId, //  oa
							siteNo :item.row.siteNo+" "+item.row.siteName, //  
							siteName :item.row.siteName, //  
							totalArea :item.row.totalArea, //  
							oaStatus :item.row.oaStatus, //  oa
							oaStatusName :oaStatusName, //  oa
							auditorId :item.row.auditorId, //  
							auditorName :item.row.auditorName, //  
							delFlag :item.row.delFlag, //  
							created:this.$api.dateFormat("yyyy-MM-dd",item.row.created),
							creator :item.row.creator, //  
							budgetId :item.row.budgetId, // 
							oaRateItemList: data
						}
						this.pageData.addDialog.dialogFormVisible = true
					}else{
						this.$message.error(res.message)
					}
				}).catch(err=>{
					setTimeout(() => {
						loading.close();
					}, 500)
					this.$message.error(""+err)
				})
			},
			queryEvt() {
				let obj = {
					oaStatus :this.pageData.tableObj.oaStatus,
					mandt :this.pageData.tableObj.mandt,
					siteNo :this.pageData.tableObj.siteNo, 
					budgetId :this.pageData.tableObj.budgetId, 
					startTime :this.pageData.tableObj.startTime, 
					endTime :this.pageData.tableObj.endTime,
					currentPage :this.pageData.tableObj.currentPage, 
					pageSize :this.pageData.tableObj.pageSize 
				}
				if(this.$api.compareDate(obj.startTime,obj.endTime)){
					this.$message({
						message: '!',
						type: 'warning'
					})
					return false
				}
				const loading = this.$loading({
					lock: true,
					spinner: 'wm-icon-loading',
					background: 'rgba(0, 0, 0, 0.7)'
				})
				fetch(this.$api.urls.POST_RATE_QUERYRATEMAIN,{
					method:'post',
					headers:{
						"Content-type":"application/json",
					},
					credentials: "include",
					body:JSON.stringify(obj)
				})
				.then(res=>res.json())
				.then(res=>{
					setTimeout(() => {
						loading.close();
					}, 500)
					if(res.success){
	        	this.pageData.tableObj.currentPage = res.model.currentPage // 
      			this.pageData.tableObj.pageSize = res.model.pageSize // 
      			this.pageData.tableObj.totalPage = res.model.pageCount // 
      			this.pageData.tableObj.totalRows = res.model.count // 
      			const data =res.model.data
      			for(let i =0;i<data.length;i++){
      				data[i].created = this.$api.dateFormat("yyyy-MM-dd",data[i].created)
      				for(let j=0;j<this.pageData.statusList.length;j++){
      					if(data[i].oaStatus == this.pageData.statusList[j].value){
      						data[i].oaStatusName = this.pageData.statusList[j].name
      						break
      					}
      				}
      				for(let j=0;j<this.pageData.budgetList.length;j++){
      					if(data[i].budgetId == this.pageData.budgetList[j].budgetId){
      						data[i].budgetName = this.pageData.budgetList[j].budgetName
      						break
      					}
      				}
      			}
      			this.pageData.tableObj.list = data
      		}else{
      			this.$message.error(res.message)
      		}
      	}).catch(err=>{
      		setTimeout(() => {
      			loading.close();
      		}, 500)
      		this.$message.error(""+err)
      	})
      },
      handleSizeChange(val) {
        this.pageData.tableObj.pageSize = val // 
        this.queryEvt()
    },
    handleCurrentChange(val) {
        this.pageData.tableObj.currentPage = val // 
        this.queryEvt()
    }
}
}
</script>

