// 
import Home from './../components/home'
import Shopsetting from './../components/shopsetting/index'
import ShopsubjectIndex from './../components/shopsubject/index'
import ShopSubject from './../components/shopsubject/subject'
import Acreage from './../components/shopcost/acreage'
import CostEntry from './../components/shopcost/costEntry'
import SpecifiedProportion from './../components/shopcost/specifiedProportion'
import Log from './../components/log/index'

// 
import ScoreLeader from './../components/employee/score/leader/index'
import ScoreLeaderDetail from './../components/employee/score/leader/detail'

import ScoreMember from './../components/employee/score/member/index'
import ScoreMemberDetail from './../components/employee/score/member/detail'

import Checkup from './../components/employee/checkup/index'
import CheckupDetail from './../components/employee/checkup/detail'
import CheckupUpload from './../components/employee/checkup/upload'

// 
import AllowanceConfirmIndex from './../components/employee/allowanceConfirm/index'
import AllowanceConfirmUpload from './../components/employee/allowanceConfirm/update'

import AllowanceImportIndex from './../components/employee/allowanceImport/index'
import AllowanceImportDetail from './../components/employee/allowanceImport/detail'
import AllowanceImportUpload from './../components/employee/allowanceImport/upload'

import SmallShopIndex from './../components/smallShop/index'
import SmallShopUpdate from './../components/smallShop/update'

export const routes = [
	{path:'/',name:'homeLink',component:Home},
	{path:'/shopsetting',name:'shopsettingLink',component:Shopsetting}, // 
	{path:'/shopSubject',name:'shopSubjectLink',component:ShopSubject}, // 
	{path:'/shopsubjectIndex',name:'shopsubjectIndexLink',component:ShopsubjectIndex}, // 
	{path:'/acreage',name:'acreageLink',component:Acreage}, // 
	{path:'/specifiedProportion',name:'specifiedProportionLink',component:SpecifiedProportion}, // 
	{path:'/costEntry',name:'costEntryLink',component:CostEntry}, // 
	{path:'/log',name:'logLink',component:Log}, // 
	{path:'*',redirect:'/'},

	// 
	{path:'/score/leader',name:'scoreLeaderLink',component:ScoreLeader}, // -
	{path:'/score/leader/detail',name:'scoreLeaderDetailLink',component:ScoreLeaderDetail}, // -

	{path:'/score/member',name:'scoreMemberLink',component:ScoreMember}, // -
	{path:'/score/member/detail',name:'scoreMemberDetailLink',component:ScoreMemberDetail}, // -

	{path:'/checkup',name:'checkupLink',component:Checkup}, // 
	{path:'/checkup/detail',name:'checkupDetailLink',component:CheckupDetail}, // -
	{path:'/checkup/upload',name:'checkupUploadLink',component:CheckupUpload}, // -

	// 
	{path:'/allowanceConfirm/index',name:'allowanceConfirmIndexLink',component:AllowanceConfirmIndex}, // 
	{path:'/allowanceConfirm/update',name:'allowanceConfirmUploadLink',component:AllowanceConfirmUpload}, // -

	{path:'/allowanceImport/index',name:'allowanceImportIndexLink',component:AllowanceImportIndex}, // 
	{path:'/allowanceImport/detail',name:'allowanceImportDetailLink',component:AllowanceImportDetail}, // -
	{path:'/allowanceImport/upload',name:'allowanceImportUploadLink',component:AllowanceImportUpload}, // -

	{path:'/smallShop/index',name:'smallShopIndexLink',component:SmallShopIndex}, // 
	{path:'/smallShop/update',name:'smallShopUpdateLink',component:SmallShopUpdate}, // 

]



