<template>
	<el-main>
		<div class="container-fluid">
			<el-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
			  <el-breadcrumb-item :to="{name:'homeLink'}"><i class="glyphicon glyphicon-home"></i></el-breadcrumb-item>
			  <el-breadcrumb-item></el-breadcrumb-item>
			  <el-breadcrumb-item></el-breadcrumb-item>
			</el-breadcrumb>			
		</div>
		<div class="rtl-custom-box mt15">
			<div class="box-hd">
				<div class="cont">
					<div class="form-horizontal">
						<el-form :inline="true" :model="pageData.queryObj" class="demo-form-inline">
							<el-row>
							  <el-col :span="6">
						  		<el-col :span="8"><label class="control-label"></label></el-col>
						  		<el-col :span="16">
						  			<el-form-item>
							  			<el-input v-model="pageData.queryObj.query0" placeholder="" />
								    </el-form-item>
						  		</el-col>
							  </el-col>
							  <el-col :span="6">
						  		<el-col :span="8"><label class="control-label"></label></el-col>
						  		<el-col :span="16">
						  			<el-form-item>
							  			<el-select v-model="pageData.queryObj.query1">
									      <el-option label="" value="shanghai"></el-option>
									    </el-select>
								    </el-form-item>
						  		</el-col>
							  </el-col>
							  <el-col :span="6" class="text-center">
						  		<el-form-item>
								    <el-button type="primary" @click="queryEvt" icon="el-icon-search"></el-button>
								  </el-form-item>
							  </el-col>
							</el-row>
						</el-form>
					</div>
				</div>
			</div>
		</div>
		<div class="box-bd pr mt15">
			<div class="tab-content custom-content">
				<div class="tab-pane in active">
					<div class="table-responsive">
						<el-table border :data="pageData.tableObj.list" style="width: 100%">
					    <el-table-column prop="date" label="ID"></el-table-column>
					    <el-table-column prop="name" label=""></el-table-column>
					    <el-table-column prop="city" label=""></el-table-column>
					  </el-table>
					</div>
					<div class="page">
						 <div class="block p15 text-center">
					    <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" 
					    	:current-page="pageData.tableObj.currentPage" 
					    	:page-sizes="pageData.tableObj.pageArray"
					      :page-size="pageData.tableObj.pageSize"
					      layout="total, sizes, prev, pager, next, jumper"
					      :total="pageData.tableObj.totalRows">
					    </el-pagination>
					  </div>
					</div>
				</div>
			</div>
		</div>
	</el-main>
</template>

<script type="text/javascript">
export default {
    data() {
      return {
      	pageData:{
      		queryObj:{
      			query0:'',
      			query1:'',
      			query2:'',
      		},
      		tableObj: {
      			currentPage: 1,
      			pageSize: 10,
      			totalPage: 1, // 
      			totalRows: 1, // 
      			pageArray:[10,20,30,40,50],
      			list: []
      		}
      	}
      }
    },
    methods: {
      queryEvt() {
        console.log('submit!');
      },
      handleSizeChange(val) {
        console.log(` ${val} `);
      },
      handleCurrentChange(val) {
        console.log(`: ${val}`);
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



